<template>
  <header class="pc-header">
    <div class="header-wrapper">
      <div class="mr-auto pc-mob-drp">
        <ul class="list-unstyled">
          <li class="dropdown pc-h-item">
            <!-- <a class="pc-head-link p-2" href="#">
              <i data-feather="bell"></i>
            </a> -->
          </li>
        </ul>
      </div>
      <div class="ml-auto">
        <ul class="list-unstyled">
          <li class="pc-h-item">
            <a class="pc-head-link mr-0 rounded" href="javascript:void(0)">
              <div class="circle">{{ userName.charAt(0) }}</div>

              <span class="px-2">
                <span class="user-name">{{ userName }}</span>
                <span class="user-desc text-secondary">{{ user_rol }}</span>
              </span>
            </a>
            <!-- <a class="pc-head-link mr-0 rounded btn-dropdown" data-toggle="dropdown" href="#">
              <div class="circle">{{ userName.charAt(0) }}</div>
             
              <span class="px-2">
                <span class="user-name">{{ userName }}</span>
                <span class="user-desc">{{ userRole }}</span>
              </span>
            </a> -->

            <!-- <div class="dropdown-menu dropdown-menu-right pc-h-dropdown">
              <router-link to="/sisge/usuario-perfil" class="pc-head-link">
                <i data-feather="user"></i>
                <span class="pl-2">
                  Perfil
                </span>
              </router-link>
              <a href="javascript:void(0)" class="pc-head-link link-data">
                <i data-feather="settings"></i>
                <span class="pl-2">Configuración</span>
              </a>
              <a href="javascript:void(0)" class="pc-head-link link-data" @click="mtdLogout">
                <i data-feather="log-out"></i>
                <span class="pl-2">Salir</span>
              </a>
            </div> -->
          </li>
        </ul>
      </div>
    </div>
  </header>
</template>
  
<script>
import { mapActions } from 'vuex';
import feather from 'feather-icons';

export default {
  name: 'LayoutNavbar',
  data() {
    return {
      user_rol: null
    }
  },
  computed: {
    userName() {
      return this.$store.getters.get__user.datos.nombres;
    },
    userRole() {
      return this.$store.getters.get__user.usu_rol;
    },
  },
  methods: {
    ...mapActions(['get', 'post']),

    getRole(){
      if(this.userRole === 'ADMIN'){
        this.user_rol = 'Administrador';
      }else if(this.userRole === 'ABOGADO'){
        this.user_rol = 'Abogado';
      }
    },

    mtdLogout: function () {
      this.$store.commit('mt_logout_auth');
      this.$router.replace({ path: '/' });
    },
  },
  mounted() {
    this.$nextTick(() => {
      feather.replace();
    });
  },

  created() {
    this.getRole();
  }
};
</script>
<style scoped>
.circle {
  width: 30px;
  height: 30px;
  background-color: #7267EF;
  color: white;
  font-weight: 800;
  border-radius: 50%;
  text-align: center;
  line-height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 15px;
}

/* .btn-dropdown{
  width: auto;
} */

.link-data {
  width: 87.5%;
}
</style>