<template>
  <div v-if="user != null">
    <LayoutNavMobile />
    <LayoutSidebar />
    <LayoutNavbar />
    <NotificationAlertas class="notification-alertas" v-if="isAbogado" />
    <router-view></router-view>
  </div>
</template>
<script>
import LayoutNavMobile from '../components/Layout/LayoutNavMobile.vue';
import LayoutNavbar from '../components/Layout/LayoutNavbar.vue';
import LayoutSidebar from '../components/Layout/LayoutSidebar.vue';
import NotificationAlertas from '../components/Notifications/NotificationAlerts.vue';

export default {
  name: 'sgel',
  components: {
    LayoutNavMobile,
    LayoutNavbar,
    LayoutSidebar,
    NotificationAlertas,
  },
  data() {
    return {
      user: null,
      rol: null,
    }
  },
  computed: {
    isAbogado() {
      return this.user?.usu_rol === 'ABOGADO';
    },
  },
  created() {
    this.user = this.$store.getters.get__user.name
    this.rol = this.$store.getters.get__user.usu_rol
    if (this.user === null) {
      this.$router.push({ path: '/' })
    }
  },
}
</script>

<style scoped>
.notification-alertas {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9999;
}
</style>


