<template>
  <nav class="pc-sidebar">
    <div class="navbar-wrapper">
      <div class="m-header bg-white">
        <a href="#" class="b-brand">
          <img src="../../assets/images/logo-unprg.png" alt="" class="img-fluid">
        </a>
      </div>
      <div class="navbar-content">
        <ul class="pc-navbar py-4">
          <li class="pc-item pc-hasmenu" @click="toggleMenu('home')">
            <router-link to="/sisge" class="pc-link" :class="{ 'bg-primary text-white rounded': getRouteHome() }">
              <span class="pr-3">
                <i data-feather="box"></i>
              </span>
              <span class="pc-mtext">Inicio</span>
            </router-link>
          </li>
          <li class="pc-item pc-hasmenu">
            <a href="javascript:void(0)" class="pc-link " @click="toggleMenu('expediente')"
              :class="{ 'selected': isOpen === 'expediente' && !getRouteExpediente(), 'bg-primary text-white rounded': getRouteExpediente() }">
              <span class="pr-3">
                <i data-feather="layers"></i>
              </span>
              <span class="pc-mtext"
                :class="{ 'text-primary': isOpen === 'expediente', 'text-white': getRouteExpediente() }">Expediente</span>
              <span class="pc-arrow" :class="{ 'rotate-down': isOpen === 'expediente' }">
                <i data-feather="chevron-right"></i>
              </span>
            </a>
            <ul class="pc-submenu" v-show="isOpen === 'expediente'">
              <li class="pc-item">
                <router-link to="/sisge/expediente/registro" class="pc-link"
                  :class="{ 'text-primary': getRouteExpedienteRegistro() }">
                  Registrar
                </router-link>
              </li>
              <li class="pc-item">
                <router-link to="/sisge/expediente/busqueda" class="pc-link"
                  :class="{ 'text-primary': getRouteExpedienteBuscar() }">
                  Buscar
                </router-link>
              </li>
            </ul>
          </li>
          <li class="pc-item pc-hasmenu">
            <a href="javascript:void(0)" class="pc-link" @click="toggleMenu('partesProcesales')"
              :class="{ 'selected': isOpen === 'partesProcesales' && !getRouteProcesal(), 'bg-primary text-white rounded': getRouteProcesal() }">
              <span class="pr-3">
                <i data-feather="users"></i>
              </span>
              <span class="pc-mtext"
                :class="{ 'text-primary': isOpen === 'partesProcesales', 'text-white': getRouteProcesal() }">Partes
                Procesales</span>
              <span class="pc-arrow" :class="{ 'rotate-down': isOpen === 'partesProcesales' }">
                <i data-feather="chevron-right"></i>
              </span>
            </a>
            <ul class="pc-submenu" v-show="isOpen === 'partesProcesales'">
              <li class="pc-item">
                <router-link to="/sisge/demandantes" class="pc-link" :class="{ 'text-primary': getRouteDemandante() }">
                  Demandantes
                </router-link>
              </li>
              <li class="pc-item">
                <router-link to="/sisge/demandados" class="pc-link" :class="{ 'text-primary': getRouteDemandado() }">
                  Demandados
                </router-link>
              </li>
            </ul>
          </li>
          <li class="pc-item pc-hasmenu" v-if="rol == 'ADMIN'">
            <a href="javascript:void(0)" class="pc-link" @click="toggleMenu('equipo')"
              :class="{ 'selected': isOpen === 'equipo' && !getRouteEquipo(), 'bg-primary text-white rounded': getRouteEquipo() }">
              <span class="pr-3">
                <i data-feather="user"></i>
              </span>
              <span class="pc-mtext"
                :class="{ 'text-primary': isOpen === 'equipo', 'text-white': getRouteEquipo() }">Equipo
                UNPRG</span>
              <span class="pc-arrow" :class="{ 'rotate-down': isOpen === 'equipo' }">
                <i data-feather="chevron-right"></i>
              </span>
            </a>
            <ul class="pc-submenu" v-show="isOpen === 'equipo'">
              <li class="pc-item">
                <router-link to="/sisge/abogados" class="pc-link" :class="{ 'text-primary': getRouteAbogado() }">
                  Abogados
                </router-link>
              </li>
            </ul>
          </li>
          <li class="pc-item pc-hasmenu">
            <a href="javascript:void(0)" class="pc-link" @click="toggleMenu('agenda')"
              :class="{ 'selected': isOpen === 'agenda' && !getRouteAgenda(), 'bg-primary text-white rounded': getRouteAgenda() }">
              <span class="pr-3">
                <i data-feather="calendar"></i>
              </span>
              <span class="pc-mtext"
                :class="{ 'text-primary': isOpen === 'agenda', 'text-white': getRouteAgenda() }">Agenda</span>
              <span class="pc-arrow" :class="{ 'rotate-down': isOpen === 'agenda' }">
                <i data-feather="chevron-right"></i>
              </span>
            </a>
            <ul class="pc-submenu" v-show="isOpen === 'agenda'">
              <li class="pc-item">
                <router-link to="/sisge/calendario" class="pc-link" :class="{ 'text-primary': getRouteCalendario() }">
                  Calendario
                </router-link>
              </li>
              <li class="pc-item">
                <router-link to="/sisge/audiencias" class="pc-link" :class="{ 'text-primary': getRouteAudiencia() }">
                  Audiencias
                </router-link>
              </li>
              <li class="pc-item">
                <router-link to="/sisge/alertas" class="pc-link" :class="{ 'text-primary': getRouteAlerta() }">
                  Alertas
                </router-link>
              </li>
            </ul>
          </li>
          <li class="pc-item pc-hasmenu" @click="toggleMenu('reporte')">
            <router-link to="/sisge/reportes" class="pc-link" :class="{ 'bg-primary text-white rounded': getRouteReporte() }">
              <span class="pr-3">
                <i data-feather="file-text"></i>
              </span>
              <span class="pc-mtext">Reportes</span>
            </router-link>
          </li>
          <li class="pc-item pc-hasmenu" v-if="rol == 'ADMIN'">
            <a href="javascript:void(0)" class="pc-link" @click="toggleMenu('mantenimiento')"
              :class="{ 'selected': isOpen === 'mantenimiento' && !getRouteMantenimiento(), 'bg-primary text-white rounded': getRouteMantenimiento() }">
              <span class="pr-3">
                <i data-feather="settings"></i>
              </span>
              <span class="pc-mtext"
                :class="{ 'text-primary': isOpen === 'mantenimiento', 'text-white': getRouteMantenimiento() }">Mantenimiento</span>
              <span class="pc-arrow" :class="{ 'rotate-down': isOpen === 'mantenimiento' }">
                <i data-feather="chevron-right"></i>
              </span>
            </a>
            <ul class="pc-submenu" v-show="isOpen === 'mantenimiento'">
              <li class="pc-item">
                <router-link to="/sisge/mantenimiento/distritos-judiciales" class="pc-link">
                  Distrito Judicial
                </router-link>
              </li>
              <li class="pc-item">
                <router-link to="/sisge/mantenimiento/instancia" class="pc-link">
                 Instancias
                </router-link>
              </li>
              <li class="pc-item">
                <router-link to="/sisge/mantenimiento/especialidad" class="pc-link">
                  Especialidad
                </router-link>
              </li>
              <li class="pc-item">
                <router-link to="/sisge/mantenimiento/materias" class="pc-link">
                 Materias
                </router-link>
              </li>
              <li class="pc-item">
                <router-link to="/sisge/mantenimiento/pretension" class="pc-link">
                  Pretensiones
                </router-link>
              </li>
              <li class="pc-item">
                <router-link to="/sisge/mantenimiento/organo-jurisdiccional" class="pc-link">
                  Organo Jurisdiccional
                </router-link>
              </li>
              <li class="pc-item">
                <router-link to="/sisge/mantenimiento/cambio-abogado" class="pc-link">
                  Cambio de Abogado
                </router-link>
              </li>
            </ul>
          </li>
          <div class="col-md-12 py-4 px-2">
            <button class="btn btn-danger w-100 rounded" @click="mtdLogout">Salir
              <span class="text-white px-2">
                <i data-feather="log-out"></i>
              </span>
            </button>
          </div>
        </ul>
      </div>
    </div>
  </nav>
</template>
  
<script>
import feather from 'feather-icons';
import PerfectScrollbar from 'perfect-scrollbar';
import 'perfect-scrollbar/css/perfect-scrollbar.css';

export default {
  name: 'LayoutSidebar',
  data() {
    return {
      isOpen: null,
    };
  },
  computed: {
    rol() {
      return this.$store.getters.get__user.usu_rol;
    },
  },
  created() {
    this.loadSelect();
  },

  mounted() {
    const container = document.querySelector('.navbar-content');
    const ps = new PerfectScrollbar(container);

    this.$once('hook:beforeDestroy', () => {
      ps.destroy();
    });

    this.$nextTick(() => {
      feather.replace();
    });
  },
  methods: {

    toggleMenu(menu) {
      this.isOpen = this.isOpen === menu ? null : menu;
      this.saveSelected();
    },

    saveSelected() {
      localStorage.setItem('isOpen', this.isOpen);
    },

    loadSelect() {
      const isOpen = localStorage.getItem('isOpen');
      if (isOpen !== null) {
        this.isOpen = isOpen;
      }
    },

    clearLocalStorage() {
      localStorage.removeItem('isOpen');
    },

    mtdLogout() {
      this.$store.commit('mt_logout_auth');
      this.$router.replace({ path: '/' });
      this.clearLocalStorage();
    },

    getRouteHome() {
      return this.$route.path === '/sisge';
    },

    getRouteExpedienteRegistro() {
      const routesToCheck = [
        '/sisge/expediente/registro'
      ];
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteExpedienteBuscar() {
      const routesToCheck = [
        '/sisge/expediente/busqueda',
        '/sisge/expediente/detalle/',
        '/sisge/expediente/lista',
        '/sisge/expediente/actualizacion/',
        '/sisge/eventos/',
        '/sisge/archivos/'
      ];
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteExpediente() {
      if (this.getRouteExpedienteRegistro() || this.getRouteExpedienteBuscar()) {
        return true;
      }
    },

    isRouteActive(route) {
      const regex = new RegExp(`^${route}\\d*$`);
      return regex.test(this.$route.path);
    },

    getRouteDemandante() {
      const routesToCheck = [
        '/sisge/demandantes',
        '/sisge/historial/registro-demandante/',
        '/sisge/demandante/informacion/'
      ]
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteDemandado() {
      const routesToCheck = [
        '/sisge/demandados',
        '/sisge/historial/registro-demandado/',
        '/sisge/demandado/informacion/'
      ]
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteProcesal() {
      if (this.getRouteDemandante() || this.getRouteDemandado()) {
        return true;
      }
    },

    getRouteAbogado() {
      const routesToCheck = [
        '/sisge/abogados'
      ]
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteEquipo() {
      if (this.getRouteAbogado()) {
        return true;
      }
    },

    getRouteCalendario() {
      return this.$route.path === '/sisge/calendario';
    },

    getRouteAudiencia() {
      return this.$route.path === '/sisge/audiencias';
    },

    getRouteAlerta() {
      return this.$route.path === '/sisge/alertas';
    },

    getRouteAgenda() {
      if (this.getRouteCalendario() || this.getRouteAudiencia() || this.getRouteAlerta()) {
        return true;
      }
    },

    getRouteReporte() {
      return this.$route.path === '/sisge/reportes';
    },

    getRouteMantenimiento() {
      const currentRoute = this.$route.path;
      return currentRoute.includes('/sisge/mantenimiento');
    }
  }
}
</script>
  
  
<style scoped>
.rotate-down {
  transform: rotate(90deg);
}

.selected {
  background-color: #e3e1fc !important;
  color: #7267ef !important;
  border-radius: 8px;
}
.navbar-content::-webkit-scrollbar {
  width: 6px;
}

.navbar-content::-webkit-scrollbar-thumb {
  background-color: #888 !important;
  ;
}

.navbar-content::-webkit-scrollbar-thumb {
  height: 20px !important;
}
</style>
  